import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import "./Navbar.css";

const Navbar = () => {
  const [gameCreators, setGameCreators] = useState({});
  const [casinos, setCasinos] = useState({});
  useEffect(() => {
    try {
      axios
        .get(`${process.env.REACT_APP_API_URL}/api/gamesupplier/`)
        .then((res) => {
          setGameCreators(res.data);
        });
      axios.get(`${process.env.REACT_APP_API_URL}/api/casino/`).then((res) => {
        setCasinos(res.data);
      });
    } catch (err) {}
  }, []);

  const [toogle, setToogle] = useState(true);

  const ShowMoreCasinos = () => {
    let rotateCasinoarrow = document.querySelector(".rotateCasinoarrow");
    if (toogle) {
      rotateCasinoarrow.style.transform = "rotate(180deg)";
      const ul = document.getElementById("casinoLinkContainer");
      console.log("casinoLinkContainer ", ul);
      let item;
      casinos.forEach((casino) => {
        item = document.createElement("a");
        item.href = `https://www.casino-blogg.se/recension-${casino.slug}`;
        item.innerText = casino.name;
        item.style.color = "white";
        item.classList.add("gameCreator");
        ul.appendChild(item);
      });
      setToogle(!toogle);
    } else {
      rotateCasinoarrow.style.transform = "rotate(360deg)";
      document.getElementById("casinoLinkContainer").innerHTML = "";
      setToogle(!toogle);
    }
  };
  const ShowMoreGameCreators = () => {
    let rotategameCreatorsarrow = document.querySelector(
      ".rotategameCreatorsarrow"
    );
    if (toogle) {
      rotategameCreatorsarrow.style.transform = "rotate(180deg)";
      const ul = document.getElementById("gameCreatorsLinkContainer");
      let item;
      gameCreators.forEach((gameCreator) => {
        item = document.createElement("a");
        item.href = `https://www.casino-blogg.se/recension-${gameCreator.slug}`;
        item.innerText = gameCreator.name;
        item.style.color = "white";
        item.classList.add("gameCreator");
        ul.appendChild(item);
      });
      setToogle(!toogle);
    } else {
      rotategameCreatorsarrow.style.transform = "rotate(360deg)";
      document.getElementById("gameCreatorsLinkContainer").innerHTML = "";
      setToogle(!toogle);
    }
  };
  return (
    <nav className="navbar navbar-dark  navbar-expand-lg  navbarContainer w-100">
      <button
        className="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarNavAltMarkup"
        aria-controls="navbarNavAltMarkup"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
        <ul className="p-0 mx-auto list-unstyled ulNavBarContainerFlex">
          <li>
            <Link className="navLinkContainer" to="/">
              Senaste casino nyheter
            </Link>
          </li>

          <li>
            <Link className="navLinkContainer" to="/casino-bonus">
              Bonus
            </Link>
          </li>

          <li>
            {" "}
            <Link className="navLinkContainer" to="/casino-spel-slots">
              Casino spel
            </Link>
          </li>

          <ul
            onClick={ShowMoreCasinos}
            className="p-0 "
            style={{ cursor: "pointer" }}
          >
            {" "}
            <Link className="navLinkContainer " to="/casino-bonus">
              Casino{" "}
              <span className="triangle-down m-1 d-inline-block rotateCasinoarrow" />
            </Link>
            <ul className="p-0 m-0" id="casinoLinkContainer">
              {/* CasinoLinks will be here.. */}
            </ul>
          </ul>

          <ul
            className="p-0 "
            onClick={ShowMoreGameCreators}
            style={{ cursor: "pointer" }}
          >
            {" "}
            <Link className="navLinkContainer " to="/casino-bonus">
              Speltillverkare{" "}
              <span className="triangle-down m-1 d-inline-block rotategameCreatorsarrow" />
            </Link>
            <ul className="p-0 m-0" id="gameCreatorsLinkContainer">
              {/* Speltillverkare will be here.. */}
            </ul>
          </ul>
        </ul>
      </div>
    </nav>
  );
};
export default Navbar;
