import React from "react";
import { Switch, Route } from "react-router-dom";
import Home from "./components/pages/Home";
import Bonus from "./components/pages/Bonus";
import Article from "./components/pages/Article";
import ArticleDetail from "./components/pages/ArticleDetail";
import CasinoSpel from "./components/pages/CasinoSpel";
import CasinoDetail from "./components/pages/CasinoDetail";
import GameCreatorDetail from "./components/pages/GameCreatorDetail";
const Routes = () => {
  return (
    <Switch>
      <Route exact path="/" component={Home} />
      <Route exact path="/casino-bonus" component={Bonus} />
      <Route exact path="/casino-spel-slots" component={CasinoSpel} />
      <Route exact path="/recension-:id" component={CasinoDetail} />
      <Route exact path="/artiklar" component={Article} />
      <Route exact path="/artikel/:id" component={ArticleDetail} />
      <Route exact path="/speltillverkare/:id" component={GameCreatorDetail} />
    </Switch>
  );
};

export default Routes;
