import React, { useState, useEffect } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import { Helmet } from "react-helmet";
import "./style/PageStyle.css";

const CasinoDetail = (props) => {
  const [casino, setCasino] = useState({});
  const [articles, setArticles] = useState([]);
  useEffect(() => {
    const slug = props.match.params.id;
    const fetchCasino = async () => {
      try {
        const res = await axios.get(
          `${process.env.REACT_APP_API_URL}/api/casino/${slug}/`
        );
        setCasino(res.data);
      } catch (err) {}
    };
    fetchCasino();

    const fetchArticles = async () => {
      const res = await axios.get(
        `${process.env.REACT_APP_API_URL}/api/article/`
      );
      // Remove the article that is activate from the list.
      const newList = res.data.filter(
        (item) => item.slug !== props.match.params.id
      );
      setArticles(newList);
    };
    fetchArticles();
  }, [props.match.params.id]);

  const getArticles = () => {
    let list = [];
    articles.map((article) => {
      return list.push(
        <div
          className="row no-gutters border rounded overflow-hidden flex-md-row mb-4 shadow-sm h-md-250 mx-auto  articleContainerMobile"
          key={article.id}
        >
          <div className="col p-4 d-flex flex-column  justify-content-between align-items-center">
            <h3 className="mb-0">{article.name}</h3>
            <Link to={`/artikel/${article.slug}`} className="stretched-link">
              Continue reading
            </Link>
          </div>
          <div className="mx-auto articleImageContainer rounded">
            <img width="200" height="180" src={article.image} alt="" />
          </div>
        </div>
      );
    });
    return list;
  };
  return (
    <React.Fragment>
      <Helmet>
        <title>{casino.meta_title}</title>
        <meta name="description" content={casino.meta_description} />
      </Helmet>
      <div>
        <div className="container">
          <br />
          <h1 className="text-center mb-5"> {casino.name}</h1>
          <img
            src={casino.casinoLogo}
            alt=""
            style={{ width: "290px" }}
            className="imageArticle m-4 rounded"
          />
          <div dangerouslySetInnerHTML={{ __html: casino.content }}></div>
          <p>{casino.meta_title}</p>
          <p>{casino.name}</p>
          <a
            href={casino.affiliate_link}
            className="affiliateButton"
            target="_blank"
          >
            {" "}
            Besök {casino.name}{" "}
          </a>
          <label style={{ fontSize: "0.7rem" }} className="mx-auto">
            {" "}
            *{casino.warningLabel}
          </label>
        </div>

        <hr />
        <div className="container mt-5">{getArticles()}</div>
      </div>
    </React.Fragment>
  );
};

export default CasinoDetail;
