import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import axios from "axios";

// Curly Brakets u use when its logic in the function.

const Article = () => {
  const [articles, setArticles] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await axios.get(
          `${process.env.REACT_APP_API_URL}/api/article/`
        );
        setArticles(res.data);
        console.log("res.data ", res.data);
      } catch (err) {}
    };
    fetchData();
  }, []);

  const getArticles = () => {
    let list = [];
    let result = [];

    articles.map((articlePost) => {
      return list.push(
        <div className="row no-gutters border rounded overflow-hidden flex-md-row mb-4 shadow-sm h-md-250 position-relative">
          <div className="col p-4 d-flex flex-column position-static justify-content-between">
            <h3 className="mb-0">{articlePost.name}</h3>
            <Link
              to={`/artikel/${articlePost.slug}`}
              className="stretched-link"
            >
              Continue reading
            </Link>
          </div>
          <div className="col-auto d-none d-lg-block">
            <img width="200" height="250" src={articlePost.image} alt="" />
          </div>
        </div>
      );
    });
    for (let i = 0; i < list.length; i += 2) {
      result.push(
        <div key={i} className="row mb-2">
          <div className="col-md-6">{list[i]}</div>
          <div className="col-md-6">{list[i + 1] ? list[i + 1] : null}</div>
        </div>
      );
    }

    return result;
  };

  return (
    <div>
      <h1>Artiklar</h1>
      <div className="container">{getArticles()}</div>
    </div>
  );
};

export default Article;
